<template>
	<div class="sub-container faq">
		<div class="page-title center">
			<h2>FAQ</h2>
		</div>

		<Accordian id="faq" :content="faqList" :multiple="true" />
	</div>
</template>

<script>
import { fetchFaq } from '@/api/faq';
import Accordian from './Accordian.vue';
export default {
	components: { Accordian },
	created() {
		this.fetchFaq();
	},
	mounted() {},
	data() {
		return {
			faqList: [],
			isLoading: false,
		};
	},
	methods: {
		async fetchFaq() {
			this.isLoading = true;
			try {
				const { data } = await fetchFaq(0);
				this.faqList = data.map(item => {
					if (item.categoryId == 'AC') {
						item.category = '계정';
					} else if (item.categoryId == 'PY') {
						item.category = '결제';
					} else if (item.categoryId == 'SH') {
						item.category = '배송';
					} else if (item.categoryId == 'PD') {
						item.category = '프로그램 및 굿즈';
					} else if (item.categoryId == 'HP') {
						item.category = '홈페이지 이용';
					} else if (item.categoryId == 'ETC') {
						item.category = '기타';
					}

					item.active = false;
					return item;
				});
				console.log(this.faqList);
			} catch (e) {
				console.log(e);
			} finally {
				this.isLoading = false;
			}
		},
	},
};
</script>

<style lang="scss">
.sub-container.faq {
	width: 700px;
}

.btn-action-delete {
	cursor: pointer;
}

.accordion {
	padding: 0;

	.accordion-item:not(:last-child) {
		border-bottom: 2px solid #e6e6e6;
	}

	.accordion-item:last-child .accordion-item-details {
		border-radius: 5px;
	}
}

.accordion-item-title {
	padding: 25px 0;
	position: relative;

	h4 {
		font-size: 1.25rem;
		margin-bottom: 0;
		padding-right: 1.25rem;
	}
}

.accordion-item-trigger {
	width: 100%;
	display: flex;
	background-color: transparent;
	border: none;
}

.accordion-category {
	padding: 0 6px;
	height: 30px;
	line-height: 30px;
	display: inline-block;
	background: #00ad84;
	box-sizing: border-box;
	margin-right: 10px;
}

.accordion-item-details-inner {
	padding: 20px;
	background-color: #fafafa;
}

.accordion-item-enter-active,
.accordion-item-leave-active {
	will-change: height;
	transition: height 0.2s ease;
}
.accordion-item-enter,
.accordion-item-leave-to {
	height: 0 !important;
}

@media screen and (max-width: 600px) {
	.sub-container.faq {
		width: 100%;
	}
}
</style>
