<template>
	<dl class="accordion box" role="presentation">
		<AccordianItem
			v-for="item in content"
			:multiple="multiple"
			:item="item"
			:groupId="groupId"
			:key="item.id"
		>
		</AccordianItem>
	</dl>
</template>

<script>
import AccordianItem from './AccordianItem.vue';
export default {
	components: { AccordianItem },
	mounted() {
		this.groupId = this.$el.id;
	},
	props: {
		content: {
			type: Array,
			required: true,
		},
		multiple: {
			type: Boolean,
			default: false,
		},
	},
	data() {
		return {
			groupId: null,
		};
	},
};
</script>

