<template>
	<div
		:id="groupId + '-' + item.id"
		class="accordion-item"
		:class="{ 'is-active': item.active }"
	>
		<dt class="accordion-item-title">
			<button @click="toggle" class="accordion-item-trigger">
				<div class="accordion-category">{{ item.category }}</div>
				<h4 class="accordion-item-title-text">{{ item.title }}</h4>
			</button>
		</dt>
		<transition
			name="accordion-item"
			@enter="startTransition"
			@after-enter="endTransition"
			@before-leave="startTransition"
			@after-leave="endTransition"
		>
			<dd v-if="item.active" class="accordion-item-details">
				<div v-html="item.contents" class="accordion-item-details-inner"></div>
			</dd>
		</transition>
	</div>
</template>

<script>
export default {
	props: ['item', 'multiple', 'groupId'],
	methods: {
		toggle(event) {
			if (this.multiple) {
				console.log(this.item.active);
				this.item.active = !this.item.active;
			} else {
				console.log(this.$parent.$children);
				this.$parent.$children.forEach(item => {
					if (
						item.$el.id === event.currentTarget.parentElement.parentElement.id
					)
						item.item.active = !item.item.active;
					else item.item.active = false;
				});
			}
		},
		startTransition(el) {
			el.style.height = el.scrollHeight + 'px';
		},

		endTransition(el) {
			el.style.height = '';
		},
	},
};
</script>

<style></style>
