// 메인화면

import { instance, instanceWithAuth } from './index';

function fetchFaqTotalCount() {
	return instance.get(`api/frequencyQuestion/totalCount`);
}

function fetchAdminFaq(page) {
	return instanceWithAuth.get(`api/admin/frequencyQuestion/all/${page}`);
}

function fetchFaqDetail(id) {
	return instance.get(`api/frequencyQuestion/detail/${id}`);
}

function fetchFaq() {
	return instance.get(`api/frequencyQuestion/all`);
}

function addFaq(data) {
	return instanceWithAuth.post('api/admin/frequencyQuestion/new', data);
}

function updateFaq(id, data) {
	return instanceWithAuth.put(`api/admin/frequencyQuestion/update/${id}`, data);
}

function deleteFaq(data) {
	return instanceWithAuth.put('api/admin/frequencyQuestion/delete', data);
}

export {
	fetchAdminFaq,
	fetchFaq,
	fetchFaqDetail,
	addFaq,
	updateFaq,
	deleteFaq,
	fetchFaqTotalCount,
};
